import { Link } from 'react-router-dom'
import React, { Component } from 'react';

class NavHeader extends Component {
	render() {
		var style = {
			width: 80,
			height: 24,
			padding: 1,
			margin: 4,
			float: 'left',
			//boxShadow: '0 0 1px #909090',
			textAlign: 'center',
			}
		return (
			<div style={{display: "table", width: '100%'}}>
				<Link to='/' style={style}>&lt;Home</Link>
				<div style={{textAlign: 'center', padding:10, fontWeight: 'bold', fontSize: 'larger'}}>Find音乐学院基本乐科</div>
			</div>
		)
	
	}
}

export default NavHeader;



