import React, { Component } from 'react';
import {Link } from 'react-router-dom'
import '../Home.css';
import Toolbar, {iconfont, color} from "../components/Toolbar";

class Home extends Component {
	constructor(props) {
		super(props)
		window.level = 1
		this.exercises = [
			{
				title: "识别练习 Identification",
				type: "Identification",
				items: [
					['note',"单音 Note"],
					['interval',"音程 Interval"],
					['chord',"和弦 Chord"],
					['keysignature',"调号 Key Signature"],
					['scale',"音阶 Scale"],
				]
			},
			{
				title: "写作练习 Construction",
				type: "Construction",
				items: [
					['note',"单音 Note"],
					['interval',"音程 Interval"],
					['chord',"和弦 Chord"],
					// ['keysignature',"调号 Key Signature"],
					['scale',"音阶 Scale"],
				]
			},
			{
				title: "弹奏练习 Keyboard",
				type: "Keyboard",
				items: [
					['note',"单音 Note"],
					['interval',"音程 Interval"],
					['chord',"和弦 Chord"],
					['scale',"音阶 Scale"],
				]
			},
			{
				title: "听辩练习 Ear Training",
				type: "EarTraining",
				items: [
					['note',"单音 Note"],
					['interval',"音程 Interval"],
					['chord',"和弦 Chord"],
					['scale',"音阶 Scale"],
				]
			},
			{
				title: "乐理测试",
				items: [
					['note',"单音 Note"],
					['interval',"音程 Interval"],
					['chord',"和弦 Chord"],
					['scale',"音阶 Scale"],
				]
			},
		]
		this.state = {
			row: 0,
			column: 0,
			level: window.level,
		}
	}
	setLevel(i) {
		window.level=i
		this.setState({level: i})
	}
	takeAction(action) {
		let newState
		switch (action.type) {
		case 'moveColumn':
			let column = this.state.column + action.offset
			let row = this.state.row
			if (column<0) {
				if (row<=0) {
					return
				}else{
					row -= 1
					let exercise = this.exercises[row]
					column = exercise.items.length-1
				}
			}else{
				let exercise = this.exercises[row]
				if (column>=exercise.items.length) {
					if (row>=this.exercises.length-1) {
						return
					}else {
						row += 1
						column = 0
					}
				}
			}
			newState = {row: row, column: column}
			break
		case 'moveRow':
		{
			let row = this.state.row + action.offset
			if (row<0 || row>=this.exercises.length) {
				return
			}
			let column = this.state.column
			let exercise = this.exercises[row]
			if (column>=exercise.items.length) {
				column = exercise.items.length-1
			}
			newState = {row: row, column: column}
		}
			break
		case 'OK':
		{
			let exercise = this.exercises[this.state.row]
			let item = exercise.items[this.state.column]
			let link = "/exercise/"+item[0]+"/"+exercise.type
			window.location.href = link
			return
		}
		default:
			break
		}
		this.setState(prevState => (newState))
	}

	render() {
		let cellWidth = window.device==="piano" ? 300 : 160
		let elems = []
		for (var i = 0; i < this.exercises.length; i++) {
			let exercise = this.exercises[i]
			let values = exercise.items;
			let items = values.map((item, index) => {
				let className = "Home-icon"
				if(this.state.row===i && this.state.column===index) {
					className += " Home-icon-hi"
				}
				return(
					<Link key={item[0]} to={'/exercise/'+item[0]+"/"+exercise.type}>
						<div className={className} style={{width: cellWidth}}>
							<div style={{display: 'table-cell', width: 64}}>
								<span className={"Home-item-icon Home-item-icon-"+item[0]} style={{backgroundPositionY: -i*32}}></span>
							</div>
							<p style={{display: 'table-cell', verticalAlign: 'middle'}}>{item[1]}</p>
						</div>
					</Link>
					)
			})
			elems.push(
				<div key={i} style={{display: 'table'}}>
					<h3>{exercise.title}</h3>
					{items}
				</div>
				)
		}
		let toolbar = <Toolbar systemButtons={{arrow: true, ok: "开始"}} action={(action) => this.takeAction(action)}/>
		return (<div style={{padding: 10}}>{elems}{toolbar}</div>)
	}
}

export default Home;



